
@font-face {
  font-family: 'Pretendard';
  src: url('/src/font/Pretendard-Light.woff') format('woff');
  font-weight: 300;
  font-style: 300;
}
@font-face {
  font-family: 'Pretendard';
  src: url('/src/font/Pretendard-Regular.woff') format('woff');
  font-weight: 500;
  font-style: 500;
}
@font-face {
  font-family: 'Pretendard';
  src: url('/src/font/Pretendard-Bold.woff') format('woff');
  font-weight: 700;
  font-style: 700;
}
@font-face {
  font-family: 'Pretendard';
  src: url('/src/font/Pretendard-Black.woff') format('woff');
  font-weight: 900;
  font-style: 900;
}
body {
  margin: 0;
  font-family: 'Pretendard';
  font-weight: 300;
  line-height: 1.5;
}

h1, h2, h3, h4, h5, h6, p, div, ul {
  padding: 0;
  margin: 0;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}